import { Component } from '@angular/core';
import { Title } from '@angular/platform-browser'

@Component({
    selector: 'demo1',
    template : `
        <div class = "demo1">
            <h3>This is my 1st component</h3>
            <h4><a (click)="setTitle('demo1')" href="" >Name    : {{Name}}</a></h4>
            <h4>Age        : {{Age}}</h4>
            <h4>Hobby      : {{Hobby.game}}</h4>
            <h4>testNumber : {{ArrStr[0]}}</h4>
            <img
                (click)="clickImg($event)" 
                src = {{Image}}            
            /> 
            <input 
                [(ngModel)] = "Name"
                class = "txtInput" 
            >
        </div>    
    `,
    styleUrls: ["./demo1.component.css"]
})
export class demo1Component{
    public constructor (private titleService: Title){}

    public setTitle(newTitle: string){
        this.titleService.setTitle(newTitle);
    }
    so:number = 0;
    Name:string = "abc";
    Age:number = 18;
    Hobby:any = {game:"Dota2", study:"Math"};
    ArrStr:string[] = ["test1", "test2", "test3"];
    Image:string = "https://tinyjpg.com/images/social/website.jpg";
    clickImg(e) {
        console.log(e);
        if(this.so==0){
            e.srcElement.src = "https://tinypng.com/images/social/photoshop.jpg";
            this.so=1;
        }
        else if(this.so==1){
            e.srcElement.src = "https://tinyjpg.com/images/social/website.jpg";
            this.so=0;
        }       
    }
}